let getPaginationItems = document.querySelector('.pagination__items');
let getPaginationArrow = document.querySelector('.pagination__arrow');
const getContainerRooms = document.querySelector('.main-second__all-rooms');
const getNumberPage = +getPaginationItems.dataset.numberpage;
let stringItems = " ";
let activeElement = 0;
let dataRooms;
const URL = 'http://localhost:5000/api/rooms';

import room from "../../room/room";
import secondPage from "../../../pages/second-page/second-pageLogic";

const addHtml = (nameImage, number, price, rating, reviews, isLux) => `<div class="room">
                  <div class="room__box"></div>
                  <div class="room__up">
                    <div class="room__image">
                      <div class="room__slider-item" style="order: 1;"> <img src="images/${nameImage}.jpg" alt="room"></div>
                      <div class="room__slider-item" style="order: 2;">2</div>
                      <div class="room__slider-item" style="order: 3;">3 </div>
                      <div class="room__slider-item" style="order: 4;">4</div>
                    </div>
                    <div class="room__arrow"><span class="room__arrow-item"></span><span class="room__arrow-item"></span></div>
                    <div class="room__circle"><span class="room__circle-item room__circle-active"></span><span class="room__circle-item"></span><span class="room__circle-item"></span><span class="room__circle-item"></span></div>
                  </div>
                  <div class="room__main">
                    <div class="room__info">
                      <div class="room__info-left"><span class="room__info-item-left">№</span><span class="room__info-item-left">${number}</span><span class="room__info-item-left">${isLux}</span></div>
                      <div class="room__info-right"><span class="room__info-item-right">${price}</span><span class="room__info-item-right">в сутки</span></div>
                    </div>
                    <div class="room__footer">
                      <div class="room__rate">
                        <div class="rate">
                          <div class="rate__body">
                            <div class="rate__active" style="width: ${rating*20}%"></div>
                            <div class="rate__inputs">
                              <input class="rate__input" type="radio" name="star-undefined" value="1">
                              <input class="rate__input" type="radio" name="star-undefined" value="2">
                              <input class="rate__input" type="radio" name="star-undefined" value="3">
                              <input class="rate__input" type="radio" name="star-undefined" value="4">
                              <input class="rate__input" type="radio" name="star-undefined" value="5">
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="room__review"><span class="room__footer-item">${reviews}</span><span class="room__footer-item">отзывов</span></div>
                    </div>
                  </div>
                  </div>`

const addItem = (number, className, secondClassName = " ") => `<span class = "${className} ${secondClassName}" href = "second-page/${number}" data-number = "${number}">` + number + "</span>";

function addItems(start, end, elementNow) {
      stringItems = " ";
      stringItems += addItem(start, "pagination__item", "pagination__active");
      for(let i = start + 1; i <= end; i++ ) { 

            stringItems += addItem(i, "pagination__item");
      
      }

      getPaginationItems.innerHTML = stringItems;

}

function changeLogic() {

}

getPaginationArrow.addEventListener('click', function() {
      activeElement++;
            if(getNumberPage - (activeElement - 1) <= 5) {
                  changeLogic()
            } else {
                  const getAllItems = document.querySelectorAll('.pagination__item');
                  getAllItems[activeElement - 1].style.display = "none";
                  getAllItems[activeElement].classList.add("pagination__active");
                  getRooms(URL, getAllItems[activeElement].dataset.number);
            }
})

async function getRooms(url, number) {
      dataRooms = await fetch(url);
      addRoms(dataRooms, number);
}
function addRoms(result, numberPage) {
      console.log(dataRooms);
      const data = result.json()
      .then(massiveRooms => {
            let strokeRooms = `<div class = "main-second__heading"> <h1> Номера, которые мы для вас подобрали </h1> </div>`;
            let endRooms = numberPage * 12;
            let startRooms = (numberPage === 1) ? 0 : (numberPage - 1) * 12;
            console.log(numberPage)
            for(i = startRooms; i < endRooms; i++) {
                  const {imageName, number, price, rating, reviews, isLux} = massiveRooms[i];
                  strokeRooms += addHtml(imageName, number, price, rating, reviews, isLux);
            }
            getContainerRooms.innerHTML = strokeRooms;
      }).then( ()=> {
            room.initRooms();
            secondPage.initSecondPage();
      });
      
}
getRooms(URL, 1);
addItems(1, getNumberPage);
